  .header__log-link {
  position: relative;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  transition: opacity 0.4s linear;
}

.header__log-link:hover {
  opacity: 0.6;
  transition: opacity 0.4s linear;
}

@media (max-width: 650px) {
  .header__log-link {
    right: 27px; 
  }
}
