.add-popup {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  align-items: center;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility .6s, opacity .6s ease-in-out;
}
.add-popup:hover {
  cursor: pointer;
}